function Project({title,thumb,genres,summary,video}){
    return (
        <div className="Project">
            <div className="Project__Column">

                <ProjectThumb thumb={thumb} alt={title}/>
            </div>
            <div className="Project__Column">
                <h1>
                    {title}
                </h1>
                <div className="Project__Genres">
                
                    {genres.map((genre,index) => <ProjectGenre genre = {genre} key = {index} />)}
                </div>
                <p className = "Project__Summary">
                    {summary}
                </p>
            </div>
         

        </div>
    )
}

Project.propTypes = {
    title : PropTypes.string.isRequired,
    thumb : PropTypes.string.isRequired,
    genre : PropTypes.string.isRequired,
    summary : PropTypes.string.isRequired,
    youtube : PropTypes.string.isRequired
}

function ProjectYoutube({youtube}){
    const opts = {
        
        height:"100%",
        width:"100%",
        playerVars: { // https://developers.google.com/youtube/player_parameters
          autoplay: 0
        }
      };
    
    return <Youtube videoId = {youtube} opts={opts}x/>
}

function ProjectGenre({genre}){
    return (
        <a className="Project__Genre" href = {genre}>{genre}</a>
    )
}

// functional component , componentwill~~ stuffs like these are too big, just use functional on ones that only has props, no render related
function ProjectThumb({thumb,alt}){
    return (
        <img src ={thumb} alt = {alt} title={alt} className = "Project__Thumbnail"></img>
    )
}

ProjectThumb.propTypes = {
    thumb : PropTypes.string.isRequired,
    alt : PropTypes.string.isRequired
}

ProjectGenre.propTypes = {
    genre : PropTypes.string.isRequired
}

ProjectYoutube.propTypes = {
    video: PropTypes.string.isRequired
}

export default Project;




